.navbar-menu{
    display: flex;
    z-index: 1000;
    
}
.navbar-menu *{
    font-family: 'Slabo 27px', serif;
}

/* Classes do botão menu */
.navbar-menu__botao {
    cursor: pointer;
    font-size: 16px;
    text-transform: uppercase;
    padding: 0.5rem;
    color:white;
    border: 1px solid white;
    background: none;
    border-radius: 20px;
}

.navbar-menu__botao--aberto:after {
    content: "";
    position: fixed;
    top: 0;
    right: 0;
    height: 100vh;
    width: 100vw;
    background: rgba(0, 0, 0, 0.5);
}

.navbar-menu__opcoes {
    list-style: none;
    color: white;
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    width: 80vw;
    padding: 2rem 1rem 0;
    margin: 0;
    background: white;
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    transition: -webkit-transform 1s ease-in-out;
    transition: transform 1s ease-in-out;
    transition: transform 1s ease-in-out, -webkit-transform 1s ease-in-out;
}

.navbar-menu__opcoes--aberto {
    -webkit-transform: translateX(0);
            transform: translateX(0);
}

.navbar-menu__opcoes--ativo {
    border-bottom: 2px solid white;
}

.navbar-menu__opcoes li + li {
    margin-top: 1rem;
}

.navbar-menu__opcoes li a {
    cursor: pointer;
    color: #ffd689;
    text-transform: uppercase;
    text-decoration: none;
    font-weight: bold;
}

@media (min-width: 992px) {
    .navbar-menu__botao {
        display: none;
    }
    
    .navbar-menu__opcoes {
        display: flex;
        align-items: center;
        position: static;
        position: initial;
        height: auto;
        height: initial;
        width: auto;
        width: initial;
        padding: 0;
        padding: initial;
        background: transparent none repeat 0 0 / auto auto padding-box border-box scroll;
        background: initial;
        -webkit-transform: none;
                transform: none;
        -webkit-transform: initial;
                transform: initial;
        transition: none 0s ease 0s;
        transition: initial;
    }
    
    .navbar-menu__opcoes--aberto {
        -webkit-transform: none;
                transform: none;
        -webkit-transform: initial;
                transform: initial;
    }
    
    .navbar-menu__opcoes--ativo {
        border-color: white;
    }
    
    .navbar-menu__opcoes li + li {
        margin-top: 0;
        margin-left: 1rem;
    }
    
    .navbar-menu__opcoes li a {
        color: white;
        padding: 0.3rem 0;
    }
    .navbar-admirar__link{
        display: none;
    }
}
