/* ImagePicker */

.image_picker{
    width:100%;
    display: flex;
    /* overflow-x: auto; */
}
.image_picker .responsive{
    position:relative;
    margin:0 6px;
    float:left;
    opacity:1;
}
.image_picker .responsive .thumbnail{
    border:1px solid #ddd;
    border-radius:4px;
    padding:5px;
    width:150px;
    opacity:1;
    margin:0;
}
.image_picker .responsive .thumbnail:hover{
    box-shadow:0 0 2px 1px rgba(255, 255, 255, 0.7);
}
.image_picker .responsive .selected{
    background-color:white;
}
.image_picker .responsive:hover{
    cursor:pointer;
}
.image_picker .responsive .checked{
    display:none;
}
.image_picker .selected{
    opacity:.7;
}
.image_picker .selected .checked{
    display:inline-block;
    transition:.5s ease;
    position:absolute;
    top:10px;
    right:10px;
}
.image_picker .selected .checked .icon{
    background:url(/static/media/checked.4bb56d88.svg);
    background-size:cover;
    height:25px;
    width:25px;
}
.image_picker .clear{
    clear:both;
}

/* Modal do Certificado */
.modal{
    width: 50%;
    height: 30%;
    display: flex;
    flex-direction: column-reverse;
}
.modal button {
    outline: none;
    border: none;
    border-radius: 50%;
    height: 28px;
    width: 28px;
    background-color: white;
    cursor: pointer;
}
.modal button:hover{
    box-shadow: 0 3px 8px 1px #e8717b91;
    transition: 0.1s linear;
}
.botao__modal--fechar{
    position: absolute;
    top: 490px;
    background: url(/static/media/cancel.629610dc.svg);
    
}
.botao__modal--download{
    position: absolute;
    top: 490px;
    margin-left: 30px;
    background: url(/static/media/download-arrow.5d2d2510.svg);    
}


